@font-face {
    font-family: PatuaOne;
    src: url(assets/PatuaOne-Regular.ttf);
    font-weight:400;
}

body {
    margin:0;
    text-align:center;
}

h1,h2,h3,h4,h5 {
    font-family:PatuaOne;
}


.profile {
    margin:60px 0 0;
    width:200px;
    height:200px;
    overflow: hidden;
    display:inline-block;
    border-radius: 50%;
}
.profile img {
    width:300px;
    margin-left:-80px;
}



.project-image {
    width:200px;
    height:260px;
    overflow:hidden;
}